html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #fff;
  background: var(--bg-primary);
  color: #333;
  color: var(--primary-text);
}

h1 {
  font-weight: 400;
}

h3 {
  font-weight: 400;
}

button {
  font-family: 'Lora', serif;
}

button:focus {
  outline: none;
}

a {
  color: #f8004f;
  color: var(--primary-color);
  cursor: pointer;
}

a:hover {
  color: #f02057;
}

span {
  color: #707070;
  color: var(--secondary-text);
}

@media (prefers-color-scheme: light) {
  :root {
    --primary-color: #f8004f;
    --primary-text: #333;
    --secondary-text: #707070;
    --light-gray: #f0f0f0;
    --bg-primary: #fff;
    --bg-secondary: #fff7fa;
    --highlight-color: #fff1f6;
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --primary-color: #fe568a;
    --primary-text: #fff;
    --secondary-text: #e2e2e2;
    --light-gray: #f0f0f0;
    --bg-primary: #232931;
    --bg-secondary: rgb(41, 49, 59);
    --highlight-color: rgb(42, 51, 61);
  }
}

div.input-div {
  position: relative;
  width: 100%;
  position: relative;
  margin: 2rem auto 1rem;
  transition: 500ms;
}

input.form-input {
  background: none;
  background-color: var(--bg-primary);
  color: var(--secondary-text);
  font-size: 0.9rem;
  padding: 1em;
  display: block;
  width: 80%;
  border-radius: 0;
  border: 1px solid var(--light-gray);
  margin: 10px 0;
  transition: 300ms ease all;
  font-weight: 500;
}

input.form-input:focus {
  outline: none;
  border: 1px solid var(--secondary-text);
}

label.form-label {
  color: var(--secondary-text);
  font-weight: normal;
  pointer-events: none;
  transition: 300ms ease all;
  position: absolute;
  left: 10px;
  transform: translateY(-2.8rem);
  transform-origin: 0%;
  transition: transform 400ms;
}

input.form-input:not(:-ms-input-placeholder) + label.form-label {
  transform: translateY(-5rem);
  opacity: 1;
}

div.input-div:focus-within label.form-label,
input.form-input:not(:placeholder-shown) + label.form-label {
  transform: translateY(-5rem);
  opacity: 1;
}

button.custom-button,
button.custom-no-invert {
  min-width: 170px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  letter-spacing: 2px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  border: 2px solid var(--primary-color);
  background-color: var(--primary-color);
  color: white;
  transition: 250ms ease all;
}

button.custom-button:hover {
  background-color: var(--bg-primary);
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

button.custom-no-invert:hover {
  -webkit-filter: brightness(0.9) contrast(1.2);
          filter: brightness(0.9) contrast(1.2);
  border: 2px solid transparent;
}

