}

.navbar {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 6.5rem;
  background-color: var(--bg-primary);
  transition: width 600ms ease;
  transition-delay: 15ms;
  overflow: hidden;
  box-shadow: 5px 0px 6px #00000029;
  z-index: 3;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
  cursor: default;
}

.nav-link,
.logo-link {
  box-sizing: border-box;
  padding-top: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 5rem;
  color: var(--secondary-text);
  fill: var(--secondary-text);
  text-decoration: none;
  border-left: 2px solid var(--bg-primary);
  cursor: pointer;
  transition: 300ms;
}

.logo-text {
  color: var(--primary-text);
  text-decoration: none;
  transition: 300ms;
}

.logo-text:hover {
  color: var(--secondary-text);
}

.nav-item > .nav-link > .link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link:not(.user-avatar):hover {
  background: var(--bg-secondary);
  color: var(--primary-color);
  fill: var(--primary-color);
  border-left: 2px solid var(--primary-color);
}
.active {
  background: var(--bg-secondary);
  color: var(--primary-color);
  fill: var(--primary-color) !important;
  border-left: 2px solid var(--primary-color);
}

.logo {
  font-size: 1.5rem;
  margin-bottom: auto;
}

.logo svg {
  width: 16px;
  margin-top: 0.4rem;
  transform: rotate(0deg);
  transition: 700ms ease;
  fill: var(--primary-text);
}

.logo-link {
  flex-direction: row;
  color: var(--primary-text);
}

.nav-link:not(.user-avatar) svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.user-avatar {
  cursor: default;
}

.user-avatar svg {
  width: 3rem;
  -webkit-filter: grayscale(50%) opacity(0.7);
          filter: grayscale(50%) opacity(0.7);
  cursor: pointer;
}

.user-avatar .link-text {
  display: none;
}

.user-avatar:hover {
  background: none;
  border-left: 2px solid var(--bg-primary);
}

.hand-held-menu {
  display: none;
}

/* Small screens */
@media only screen and (max-width: 700px) {
  .navbar {
    bottom: 0;
    top: auto;
    top: initial;
    width: 100vw;
    height: 4rem;
  }

  .active {
    background: var(--bg-secondary);
    color: var(--primary-color);
    fill: var(--primary-color) !important;
    border-bottom: 2px solid var(--primary-color);
    border-left: 0;
  }

  .logo {
    display: none;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
    padding-top: 10%;
  }

  .user-avatar {
    padding-bottom: 10px;
    padding-top: 0;
  }

  .nav-link:not(.user-avatar):hover {
    border-left: 2px solid var(--bg-primary);
  }

  .open ~ .hand-held-menu {
    background-color: var(--bg-primary);
    box-shadow: 0px -4px 10px #0000007d;
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    box-sizing: border-box;
    padding: 4%;
    z-index: 3;
    right: 0;
    bottom: 5em;
  }

  .open ~ .hand-held-menu > span,
  .open ~ .hand-held-menu > button {
    margin-top: 5px;
    background: transparent;
    border: none;
    outline: none;
    padding: 0;
    color: var(--primary-color);
    cursor: pointer;
    text-align: left;
  }
}

@media only screen and (min-width: 700px) {
  .open {
    width: 14rem;
  }
  .open .nav-link {
    flex-direction: row;
    justify-content: center;
    padding-top: 0;
    padding-right: 15%;
  }
  .open .nav-link .link-text {
    display: block;
  }

  .open .logo svg {
    transform: rotate(-180deg);
  }

  .open .user-avatar .link-text {
    display: block;
    margin-left: 10px;
  }

  .open .user-avatar .link-text > span,
  .open .user-avatar .link-text > a {
    display: block;
    margin-top: 5px;
  }

  .open .user-avatar .link-text > button.user-logout {
    margin-top: 5px;
    background: transparent;
    border: none;
    outline: none;
    padding: 0;
    color: var(--primary-color);
    cursor: pointer;
  }

  .open .user-avatar .link-text > button.user-logout:hover {
    color: #f02057;
  }

  .open .user-avatar .link-text > a.user-username:hover {
    color: var(--primary-color);
    cursor: pointer;
  }

  .user-username.active {
    background: var(--bg-primary);
    color: var(--primary-color);
    fill: var(--text-primary) !important;
    border-left: 2px solid transparent;
  }
}

